import React, { useState } from 'react';
import PropTypes from 'prop-types';
// Design Resources
import { Button, Input, Popover, Space } from 'antd';
import { FileUnknownOutlined } from '@ant-design/icons';
// Components
import { Instruction, Title } from '../../shared';
import Dial from './Dial';

const getHint = (card) => {
  if (card.target === 0) {
    return 'nmero 0, bem no centro,';
  }

  if (card.target < 0) {
    return `"${Math.abs(card.target)}" do lado "${card.left}"`;
  }

  return `"${card.target}" do lado "${card.right}"`;
};

function Rules() {
  return (
    <ul>
      <li>
        Use uma nica idea. Evite usar "mas", "enquanto", "quando", e tambm superlativos "super", "muito"
      </li>
      <li>No invente coisas. Exemplo: 'Nicolas Cage cantando uma msica dos Beatles'  invlido.</li>
      <li>Mantenha-se no assunto da carta. Exemplo: 'Amor' no  uma dica vlida para 'Sujo'.</li>
      <li>No use nmeros para sugerir a posio do espectro.</li>
      <li>No use partes, derivados ou sinnimos das palavras da carta.</li>
    </ul>
  );
}

function DialClueWriting({ onSendClue, card }) {
  const [clue, setClue] = useState('');

  const onEnterInput = (e) => {
    if (e.key === 'Enter') {
      onSendClue({ clue });
    }
  };

  return (
    <div className="o-dial-clue-writing">
      <Title>Escreva uma dica!</Title>
      <Instruction contained>
        Escreva uma dica que ajudar seu time a apontar o nmero {getHint(card)} do medidor de ondas
        telepticas.
        <br />
        <Popover title="Regras" content={Rules}>
          <Button type="link" icon={<FileUnknownOutlined />}>
            Regras
          </Button>
        </Popover>
      </Instruction>

      <Dial card={card} showTarget target={card.target} />

      <Space className="container container--center container--transparent">
        <Input
          onChange={(e) => setClue(e.target.value.toUpperCase())}
          placeholder="Digite sua dica aqui"
          className="uppercase-input"
          onKeyPress={onEnterInput}
        />
        <Button
          type="primary"
          disabled={!Boolean(clue)}
          onClick={() => {
            onSendClue({ clue });
          }}
        >
          Enviar dica
        </Button>
      </Space>
    </div>
  );
}

DialClueWriting.propTypes = {
  onSendClue: PropTypes.func.isRequired,
  card: PropTypes.shape({
    id: PropTypes.string,
    left: PropTypes.string,
    right: PropTypes.string,
  }).isRequired,
};

export default DialClueWriting;
